:root {
    --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    --animation-duration: 250ms;
}

li {
    list-style: none;
}

.search-form {
    display: flex;
    justify-content: center;
    background-color: aqua;
}

.search-form-btn {
    margin-left: 10px;
}

.load-more {
    display: none;
}

.load-more.is-hidden {
    display: flex;
    margin: 0 auto;
    margin-top: 15px;
}

.gallery {
     display: flex; 
     flex-wrap: wrap; 
    grid-gap: 10px;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;   
    margin-top: 15px;
}

.gallery-card {
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.gallery-card__image:hover {
    transform: scale(1.03);
}

.gallery-card__image {
    display: block;
     height: 240px;
    width: 320px; 
object-fit: cover;
    transition: transform var(--animation-duration) var(--timing-function);
    
}

.gallery-card__link {
    display: block;
    text-decoration: none;
    
}  

.gallery-info {
    color: burlywood;
    display: flex;
    text-align: center;
    justify-content: center;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14),
    0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.gallery-info__item {
    display: flex;
    flex-direction: column;
}

.gallery-info__item:not(:last-child) {
    margin-right: 10px;
}